import * as React from "react";
import { useUserInfo } from "shared/components/save";
import Defer from "shared/components/ui/Defer";
import { useFetcher } from 'shared/components/ajax';
import Navbar from "shared/components/ui/Navbar";
import { Button, List, ListItem, Typography, withStyles, ListItemText, ListItemSecondaryAction } from "@material-ui/core";
import { navigate } from "gatsby";

/**
 * @type {import("@material-ui/core/styles/withStyles").Styles<any, any>}
 */
const styles = theme => ({
    head: {
        display: 'flex',
        alignItems: 'center',
    },
    button: {
        margin: 20,
        width: 'calc(100% - 40px)',
        '& .MuiButton-label': {
            flexDirection: 'column',
            display: 'flex',
        },
        '& .MuiSvgIcon-root': {
            marginBottom: theme.spacing(1),
            objectFit: 'contain',
            width: '40px',
            height: '40px',
        }
    },
});

const Page = withStyles(styles)(({ classes }) => {
    const user = useUserInfo();
    const driver = useFetcher(user && user.features.driver && '/delivery/driver/info');

    return (
        <>
            <Navbar />
            <Defer data={user}>
                {() => (
                    <List>
                        {user.features.driver && <Defer data={driver}>
                            {() => (<ListItem>
                                <div className={classes.head}>
                                    <div>
                                        <Typography>{driver.data.title}</Typography>
                                    </div>
                                </div>
                            </ListItem>)}
                        </Defer>}
                        {!user.features.driver && <ListItem>
                            <ListItemText primary="Anda belum mendaftar sebagai agen driver" />
                            <ListItemSecondaryAction>
                                <Button variant="contained" color="primary"
                                    onClick={() => navigate('/driver/register')}>Daftar disini</Button>
                            </ListItemSecondaryAction>
                        </ListItem>}
                    </List>
                )}
            </Defer>
        </>
    );
});

export default Page;
